@tailwind base;

@tailwind components;

@tailwind utilities;

@tailwind screens;

@import 'https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900';
@import 'https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400;700&display=swap';
