body {
	font: 400 14px/1.8 'Poppins', sans-serif;
	color: #fff;
	overflow-x: hidden;
	background-color: #1f1f20;
}

h1, h2, h3, h4, h5, h6 {
	font-family: 'Josefin Sans', sans-serif;
}

:root {
	--recruiter-mode-display: none;
}

.recruiter-mode {
	display: var(--recruiter-mode-display, none);
}